#adminmanager-logout{
  float:right;
  margin-top: 10px;
  margin-right: 10px;
  display: block;
}
.infor{
  padding-bottom: 100px;
  width: 80%;
  display: block;
  border: 1px solid #ccc ;
  margin: 50px auto;
}
.informationProduct .infor .c4{width: 50%;height: 100%;float: left;}
.informationProduct .infor .c6{width: 50%;height: 100%;float: left;}
.informationProduct .infor .cropmimg img{width: 100%;height:100%;padding-bottom: 30px;}
.informationProduct .infor .cropmimg {width: 80%;height:80%;margin: 15px auto; display: block}
.informationProduct .infor .btnControl{margin-top: 30px;}
.informationProduct .infor .btnControl button{
  width: 30%; margin:0 auto;display: block;background: cornflowerblue;
  outline: none;border: none;padding: 5px 30px;
}
.informationProduct .infor .row{width: 90%;}
.informationProduct .infor .row{margin: 30px 0; border-bottom:1px solid #ccc; }
.informationProduct .infor .row p{margin: 0;height: 10%;font-size:16px;text-align: center;padding: 5px 0;}
.informationProduct .infor .row .title-infor {width: 40%;float:left;}
.informationProduct .infor .row .data{width: 60%;float:left;border-left:1px solid #ccc;}
.informationProduct .infor .row input{
  width: 60%;height: 30px;display: block;
  border-bottom: none;border-top:1px solid #ccc;border-left:1px solid #ccc;border-right:1px solid #ccc;
  outline: none;padding: 0 10px;font-size: 16px;
}
.informationProduct .propety{
  height: 540px;
  padding-bottom: 40px;
  border-bottom:1px solid #ccc;
}
.informationProduct .describle textarea{
  outline: none;
}
.informationProduct .describle .title-describle{
  margin-left: 15px;font-size: 18px;
  font-weight: bold;
}
.informationProduct .describle .data{
  margin-left: 15px;
}
.informationProduct .clr{clear: both;}
/*--------------------------------------------------------------------------------------------*/
.admin-menu .menu li a{margin: 0 5px;}
.router-page{}
.router-page li{list-style: none;display: inline-table;margin-right: 5px;}
.product-img{
  width: 50px;
}
.productStatus{margin-left: 20px}
/*---------------------------------------------------------------------------------------------*/
.informationProduct .backToTable{background: none;outline: none;border: none; margin-left: 20px;}
.informationProduct .id-product{float: left;margin: 15px 20px;
;padding: 5px 10px;border: 1px solid #ccc;border-radius: 15px;
}
.informationProduct .addImage{
  margin: 0 auto;
}
.informationProduct .editProduct{
  background: none;outline: none;border: none;font-size: 14px;
  margin-left: 50%;
  margin-top:15px;padding: 5px 30px;border-radius: 5px;background: #5BC0DD;
}
.informationProduct .cancelEdit{
  background: none;outline: none;border: none;font-size: 14px;padding: 5px 30px;
  border-radius: 5px;background: #ff0033;
  margin-left: 10px;margin-top:15px
}
.informationProduct .describle textarea{margin-left: 15px;width: 80%;height:100px}
.informationProduct .addImgae img{ }
.informationProduct .addImgae input{margin:  0 auto}
#messageImg{
  margin-top: 15px;
  text-align: center;
  font-size: 14px;
  color:red;
}


